'use client';

import { useState, useEffect, useRef } from 'react';

export default function GeolocationPage() {
  const mapRef = useRef(null);
  const mapInstanceRef = useRef(null);
  const markersRef = useRef([]);
  const [selectedDevice, setSelectedDevice] = useState(null);
  const [showDetailModal, setShowDetailModal] = useState(false);
  const [autoRefresh, setAutoRefresh] = useState(true);
  const [lastUpdate, setLastUpdate] = useState(new Date());
  const [statusFilter, setStatusFilter] = useState('all');
  const [complianceFilter, setComplianceFilter] = useState('all');
  const [leafletLoaded, setLeafletLoaded] = useState(false);

  const [devices] = useState([
    { id: 1, name: 'iPhone 14 Pro', employee: 'John Doe', type: 'Mobile', os: 'iOS 17.2', status: 'online', compliant: true, battery: 85, location: 'New York, NY', coordinates: [40.7128, -74.006], lastSync: '2 mins ago', speed: 0 },
    { id: 2, name: 'MacBook Pro', employee: 'Sarah Smith', type: 'Laptop', os: 'macOS 14.2', status: 'online', compliant: true, battery: 92, location: 'San Francisco, CA', coordinates: [37.7749, -122.4194], lastSync: '5 mins ago', speed: 0 },
    { id: 3, name: 'Samsung Galaxy S23', employee: 'Mike Johnson', type: 'Mobile', os: 'Android 14', status: 'offline', compliant: false, battery: 45, location: 'Chicago, IL', coordinates: [41.8781, -87.6298], lastSync: '2 hours ago', speed: 0 },
    { id: 4, name: 'iPad Pro', employee: 'Emily Chen', type: 'Tablet', os: 'iPadOS 17.2', status: 'online', compliant: true, battery: 78, location: 'Los Angeles, CA', coordinates: [34.0522, -118.2437], lastSync: '1 min ago', speed: 25 },
    { id: 5, name: 'Dell XPS 15', employee: 'David Wilson', type: 'Laptop', os: 'Windows 11', status: 'online', compliant: false, battery: 65, location: 'Austin, TX', coordinates: [30.2672, -97.7431], lastSync: '10 mins ago', speed: 0 },
    { id: 6, name: 'iPhone 13', employee: 'Lisa Martinez', type: 'Mobile', os: 'iOS 17.1', status: 'offline', compliant: true, battery: 30, location: 'Boston, MA', coordinates: [42.3601, -71.0589], lastSync: '1 hour ago', speed: 0 },
    { id: 7, name: 'MacBook Air', employee: 'Robert Taylor', type: 'Laptop', os: 'macOS 14.1', status: 'online', compliant: true, battery: 88, location: 'Seattle, WA', coordinates: [47.6062, -122.3321], lastSync: '3 mins ago', speed: 0 },
    { id: 8, name: 'Samsung Tab S9', employee: 'Jennifer Lee', type: 'Tablet', os: 'Android 14', status: 'online', compliant: false, battery: 55, location: 'Miami, FL', coordinates: [25.7617, -80.1918], lastSync: '7 mins ago', speed: 15 },
  ]);

  // Load Leaflet dynamically (client-only)
  useEffect(() => {
    if (typeof window === 'undefined') return;
    const link = document.createElement('link');
    link.rel = 'stylesheet';
    link.href = 'https://unpkg.com/leaflet@1.9.4/dist/leaflet.css';
    document.head.appendChild(link);

    const script = document.createElement('script');
    script.src = 'https://unpkg.com/leaflet@1.9.4/dist/leaflet.js';
    script.onload = () => setLeafletLoaded(true);
    document.body.appendChild(script);

    return () => {
      document.head.removeChild(link);
      document.body.removeChild(script);
    };
  }, []);

  // Initialize Map
  useEffect(() => {
    if (!leafletLoaded || !mapRef.current || mapInstanceRef.current) return;
    const L = window.L;
    const map = L.map(mapRef.current).setView([39.8283, -98.5795], 4);
    L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
      attribution: '¬© OpenStreetMap contributors',
      maxZoom: 19,
    }).addTo(map);
    mapInstanceRef.current = map;
    updateMarkers();
  }, [leafletLoaded]);

  useEffect(() => {
    if (mapInstanceRef.current && leafletLoaded) {
      updateMarkers();
    }
  }, [statusFilter, complianceFilter]);

  const updateMarkers = () => {
    if (!mapInstanceRef.current || !window.L) return;
    const L = window.L;
    const map = mapInstanceRef.current;
    markersRef.current.forEach(marker => marker.remove());
    markersRef.current = [];

    const filtered = devices.filter(device => {
      if (statusFilter !== 'all' && device.status !== statusFilter) return false;
      if (complianceFilter === 'compliant' && !device.compliant) return false;
      if (complianceFilter === 'non-compliant' && device.compliant) return false;
      return true;
    });

    filtered.forEach(device => {
      const color = device.status === 'offline' ? '#9CA3AF' : device.compliant ? '#10B981' : '#EF4444';
      const icon = L.divIcon({
        className: 'custom-marker',
        html: `
          <div style="
            width: 40px; height: 40px; 
            background: ${color}; 
            border-radius: 50%; 
            border: 3px solid white; 
            box-shadow: 0 4px 6px rgba(0,0,0,0.3);
            display: flex; 
            align-items: center; 
            justify-content: center;
            animation: ${device.status === 'online' ? 'pulse 2s infinite' : 'none'};
          ">
            <span style="font-size: 20px;">üìç</span>
          </div>`,
        iconSize: [40, 40],
        iconAnchor: [20, 40],
      });

      const popupHtml = `
        <div style="font-family: system-ui; min-width: 200px;">
          <h3 style="margin: 0 0 8px 0; font-size: 16px; font-weight: 600;">${device.name}</h3>
          <p style="margin: 4px 0; color: #6B7280;">üë§ ${device.employee}</p>
          <p style="margin: 4px 0; color: #6B7280;">üìç ${device.location}</p>
          <p style="margin: 4px 0; color: #6B7280;">üîã ${device.battery}%</p>
          ${device.speed > 0 ? `<p style="margin: 4px 0; color: #6B7280;">üöó ${device.speed} mph</p>` : ''}
          <div style="margin-top: 8px; padding: 4px 8px; background: ${
            device.compliant ? '#D1FAE5' : '#FEE2E2'
          }; color: ${device.compliant ? '#065F46' : '#991B1B'}; border-radius: 4px; font-size: 12px; text-align: center;">
            ${device.compliant ? '‚úì Compliant' : '‚ö† Non-Compliant'}
          </div>
        </div>
      `;

      const marker = L.marker(device.coordinates, { icon }).addTo(map).bindPopup(popupHtml);
      marker.on('click', () => {
        setSelectedDevice(device);
        setShowDetailModal(true);
      });
      markersRef.current.push(marker);
    });

    if (filtered.length > 0) {
      const bounds = L.latLngBounds(filtered.map(d => d.coordinates));
      map.fitBounds(bounds, { padding: [50, 50] });
    }
  };

  // Center and refresh controls
  const centerMap = () => {
    if (mapInstanceRef.current && window.L) {
      const filtered = devices.filter(device => {
        if (statusFilter !== 'all' && device.status !== statusFilter) return false;
        if (complianceFilter === 'compliant' && !device.compliant) return false;
        if (complianceFilter === 'non-compliant' && device.compliant) return false;
        return true;
      });
      if (filtered.length > 0) {
        const bounds = window.L.latLngBounds(filtered.map(d => d.coordinates));
        mapInstanceRef.current.fitBounds(bounds, { padding: [50, 50] });
      }
    }
  };

  const locateOnMap = (device) => {
    if (mapInstanceRef.current) {
      mapInstanceRef.current.flyTo(device.coordinates, 13, { duration: 1.5 });
      setShowDetailModal(false);
    }
  };

  // Auto-refresh clock
  useEffect(() => {
    if (autoRefresh) {
      const interval = setInterval(() => {
        setLastUpdate(new Date());
      }, 10000);
      return () => clearInterval(interval);
    }
  }, [autoRefresh]);

  const stats = {
    total: devices.length,
    online: devices.filter(d => d.status === 'online').length,
    inMotion: devices.filter(d => d.speed > 0).length,
    compliant: devices.filter(d => d.compliant).length,
  };

  const filteredDevices = devices.filter(device => {
    if (statusFilter !== 'all' && device.status !== statusFilter) return false;
    if (complianceFilter === 'compliant' && !device.compliant) return false;
    if (complianceFilter === 'non-compliant' && device.compliant) return false;
    return true;
  });

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-100">
      <style>{`
        @keyframes pulse {
          0%, 100% { transform: scale(1); opacity: 1; }
          50% { transform: scale(1.1); opacity: 0.8; }
        }
      `}</style>

      {/* HEADER */}
      <div className="p-4 bg-white shadow-sm border-b">
        <h1 className="text-2xl font-bold text-blue-600">üó∫Ô∏è Geolocation Tracking</h1>
        <p className="text-sm text-gray-500">Monitor devices in real-time across locations</p>
      </div>

      {/* FILTERS + MAP */}
      <div className="p-6 grid grid-cols-3 gap-6">
        <div className="col-span-2 bg-white rounded-2xl shadow-sm border p-4 relative">
          <div ref={mapRef} className="w-full h-[600px]" />
          {!leafletLoaded && (
            <div className="absolute inset-0 flex items-center justify-center bg-gray-50">
              <div className="text-center">
                <div className="w-10 h-10 border-4 border-blue-500 border-t-transparent rounded-full animate-spin mx-auto mb-2"></div>
                <p>Loading map...</p>
              </div>
            </div>
          )}
        </div>

        <div className="bg-white rounded-2xl shadow-sm border overflow-hidden">
          <div className="p-4 border-b">
            <h3 className="font-bold text-lg">Devices ({filteredDevices.length})</h3>
          </div>
          <div className="overflow-y-auto h-[560px]">
            {filteredDevices.map((device) => (
              <div
                key={device.id}
                onClick={() => locateOnMap(device)}
                className="p-4 border-b hover:bg-gray-50 cursor-pointer"
              >
                <div className="flex items-start justify-between mb-2">
                  <div>
                    <h4 className="font-semibold">{device.name}</h4>
                    <p className="text-sm text-gray-500">üë§ {device.employee}</p>
                  </div>
                  <div
                    className={`w-3 h-3 rounded-full ${
                      device.status === 'online' ? 'bg-green-500' : 'bg-gray-400'
                    }`}
                  ></div>
                </div>
                <p className="text-xs text-gray-600">üìç {device.location}</p>
                <p className="text-xs text-gray-600">üîã {device.battery}%</p>
                <p className="text-xs text-gray-600">‚è±Ô∏è {device.lastSync}</p>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}